@import "../../styles/sass/variables";
@import "../../styles/sass/mixins";

.aboutMe__block{
    padding: 100px 0 80px 0;
    background: $aboutMe-background;
    .container {
        @include flex (center,space-between,row);
        .aboutMe__photowrapper {
            background: rgba(0, 0, 0, 0) url(../../assets/profile-bg.png) no-repeat scroll 0 100%;
            margin-right: -30px;
            padding-bottom: 40px;
            padding-left: 30px;
            .aboutMe__photo {
                height: 450px;
                width: 400px;
                background: peachpuff;
                border: 10px solid $border-color;
            }
        }
        .aboutMe__article{
            height: 100%;
            width: 500px;
            .aboutMe__text{
                font-size: 14px;
                line-height: 24px;
                color: $p-color;
                margin: 0 0 10px;
                font-family: 'Lato', sans-serif;
            }
            .aboutMe__block__button__block{
                display: flex;
                .downloadCV {
                    display: block;
                    color: $primary-color-text;
                    background-color: $secondary-color-text;
                    font-size: 12px;
                    text-transform: uppercase;
                    letter-spacing: 2px;
                    border: 2px solid rgba(0, 0, 0, 1);
                    padding: 14px 30px 14px 30px;
                    transition: background-color .3s ease 0s, color .3s ease 0s, border-radius .3s ease 0s;
                    white-space: nowrap;
                    &:hover {
                        color: $secondary-color-text;
                        background-color: $primary-color-text;
                    }
                }
            }
            .aboutMe__block__button__block >button + a {
                margin-left: 30px;
            }
        }
    }
}









